<template>
	<div :class="scaleClass"> 
		<div class="tip">
			<p>温馨提醒：表单界面右侧有选项卡，必须将基本信息和详情填写完毕才可以提交表单</p>
		</div>
		<el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px" style="text-align: left;">
			<el-tabs  tab-position="right">
			    <el-tab-pane label="基本信息">
			    	<el-form-item label="商品名称" prop="spmc">
						<el-input type="text" v-model="ruleForm.spmc" placeholder="商品名称" clearable maxlength="60" autocomplete="off"></el-input>
			    	</el-form-item>
					<el-form-item label="采购编码" prop="itemno">
						<el-input type="text" v-model="ruleForm.itemno" placeholder="采购编码" clearable maxlength="40" autocomplete="off" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="单位" prop="dw">
						<el-select v-model="ruleForm.dw" placeholder="单位" clearable filterable >
						    <el-option
						      v-for="item in ruleForm.dws"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
					    </el-select>
					</el-form-item>
					<el-form-item label="款号" prop="spcode">
						<el-input type="text" v-model="ruleForm.spcode" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="商品类别" prop="lbid" >
						<el-cascader
							filterable
							:show-all-levels="false"
							expand-trigger="hover"
						    :options="ruleForm.lboptions"
						    v-model="ruleForm.lbidArr"
						    @change="handleChangelb">
						</el-cascader>
					</el-form-item>
					<el-form-item label="材质" prop="cz">
						<el-input type="text" v-model="ruleForm.cz" placeholder="材质" clearable maxlength="20" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="品牌" prop="brand">
						<el-input type="text" v-model="ruleForm.brand" placeholder="品牌" clearable maxlength="20" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="价格" prop="costprice">
						<el-input-number v-model="ruleForm.costprice" controls-position="right"  :min="1"></el-input-number>
					</el-form-item>
					<el-form-item label="商品描述" prop="productdesc">
						<el-input type="textarea" v-model="ruleForm.productdesc"></el-input>
					</el-form-item>
			    </el-tab-pane>
			    <el-tab-pane label="详情">
			   		<el-form-item prop="cm">
						<el-select v-model="form.cm" placeholder="尺码" clearable filterable>
						    <el-option
						      v-for="item in ruleForm.cms"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
					    </el-select>
					    <el-button type="primary" @click="handleCmYsSl">添加</el-button>
					</el-form-item>
					<el-form-item>
						<el-table
						      :data="ruleForm.tableData"
						      style="width: 100%">
		<!--				       <el-table-column
						        prop="id"
						        label="ID"
						        >
						      </el-table-column>-->
						      <el-table-column
						        prop="cm"
						        label="尺码"
						        >
						      </el-table-column>
						      <el-table-column
						        prop="ys"
						        label="颜色"
						        >
						      </el-table-column>
						      <el-table-column
						        prop="jdsl"
						        label="数量"
						        >
						      </el-table-column>
						      <el-table-column
							      fixed="right"
							      label="操作"
							      width="360">
							      <template slot-scope="scope">
							      	<el-row>
								        <el-button type="success" size="small" icon="el-icon-edit" @click.native.prevent="updCmYsSl(scope.row.id)">修改</el-button>
								        <el-button type="danger"  size="small" icon="el-icon-delete" @click.native.prevent="delteleCmYsSl(scope.row.index)">删除</el-button>
							      	</el-row>
							      </template>
							  </el-table-column>
					    </el-table>
					</el-form-item>
			    </el-tab-pane>
			</el-tabs>
			<el-form-item>
				<el-button type="primary" @click.native.prevent="submitForm" :loading="loading">提交</el-button>
				<el-button  @click.native.prevent="gotoParentPage" >返回</el-button>
			</el-form-item>
		</el-form>
		<el-dialog title="指定颜色尺码" :visible.sync="dialogFormVisible" width="400px">
		  <el-form ref="form" :model="form" :rules="rules" label-width="60px" style="text-align: left;">
		    <el-form-item label="颜色" prop="ys">
		      <el-input v-model="form.ys" autocomplete="off" clearable></el-input>
		    </el-form-item>
		    <el-form-item label="数量" prop="jdsl">
		      <el-input v-model="form.jdsl" type="number" autocomplete="off"></el-input>
		    </el-form-item>
		  </el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click.native.prevent="dialogFormVisible = false">取消</el-button>
		    <el-button type="primary" @click.native.prevent="addYsSl">确定</el-button>
		  </span>
		</el-dialog>
		<el-dialog title="修改尺码颜色数量" :visible.sync="dialogFormCmYsSlVisible" width="400px">
		  <el-form ref="forms" :model="forms" :rules="rules" label-width="60px" style="text-align: left;">
		  	<el-form-item label="尺码" prop="cms">
		  		<el-select v-model="forms.cms" placeholder="尺码" clearable filterable>
				    <el-option
				      v-for="item in ruleForm.cms"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
			    </el-select>
		      <!--<el-input v-model="forms.cms" autocomplete="off" clearable></el-input>-->
		    </el-form-item>
		    <el-form-item label="颜色" prop="yss">
		      <el-input v-model="forms.yss" autocomplete="off" clearable></el-input>
		    </el-form-item>
		    <el-form-item label="数量" prop="jdsls">
		      <el-input v-model="forms.jdsls" type="number" autocomplete="off"></el-input>
		    </el-form-item>
		  </el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click.native.prevent="dialogFormCmYsSlVisible = false">取消</el-button>
		    <el-button type="primary" @click.native.prevent="updateCmYsSl(forms.id)">确定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>
<script>
  import update from '../../../../assets/js/nav/sptb/child/update.js'
  export default update
</script>
<style scoped lang="scss" >
@import '../../../../assets/css/nav/sptb/child/update.scss'
</style>


