export default {
		data() {
			return {
				form:{
					ys:'',//颜色
					cm:'',//尺码
					jdsl:'',//数量
				},
				forms:{
					index:'',
					yss:'',//颜色
					cms:'',//尺码
					jdsls:'',//数量
				},
				loading:false,
				dialogFormVisible:false,
				dialogFormCmYsSlVisible:false,
				rules:{
						cms:[
							{
							required:true,
			      			message:'请输入尺码',
			      			trigger:'blur'
							}
						],
						yss:[
							{
							required:true,
			      			message:'请输入商品颜色',
			      			trigger:'blur'
							}
						],
						jdsls:[
							{
							required:true,
			      			message:'请输入商品数量',
			      			trigger:'blur'
							}
						],
						ys:[
							{
							required:true,
			      			message:'请输入商品颜色',
			      			trigger:'blur'
							}
						],
						jdsl:[
							{
							required:true,
			      			message:'请输入商品数量',
			      			trigger:'blur'
							}
						],
						spmc:[
							{
							required:true,
			      			message:'请输入商品名称',
			      			trigger:'blur'
							}
						],
						itemno:[
							{
							required:true,
			      			message:'请输入采购编码',
			      			trigger:'blur'
							}
						],
						dw:[
							{
							required:true,
			      			message:'请选择采购单位',
			      			trigger:'blur'
							}
						],
						lbid:[
							{
							required:true,
			      			message:'请选择商品类别',
			      			trigger:'blur'
							}
						],
						cz:[
							{
							required:true,
			      			message:'请输入材质',
			      			trigger:'blur'
							}
						],
						costprice:[
							{
							required:true,
			      			message:'请录入价格',
			      			trigger:'blur'
							}
						]
				},
				scaleClass:'',
				ruleForm: {					
					spmc: '',//商品名称
					itemno:'',//编码
					dw:'',//单位
					spcode:'',//款号
					lbid:'',//类别id
					lbidArr:[],
					lbmc:'',//类别名称
					cz:'',//材质
					brand:'',//品牌
					costprice:'',//价格
					productdesc:'',//描述
					ys:'',//颜色
					cm:'',//尺码
					jdsl:'',//数量
					dws: [],
					lboptions:[],
					cms:[],
					tableData:[]
				},
			}
		},
	    created() {
		 	this.$api.page('productlb/getProductLbPageList',  {},localStorage.token, success => {
	         	let lbArray=new Array();
	         	lbArray.length=0;
	         	for(let i=0;i<success[0].children.length;i++){
	         		let lbChildrenArray=new Array();
	         		lbChildrenArray.length=0;
	         		if(success[0].children[i].children.length>0){
	         			for(let j=0;j<success[0].children[i].children.length;j++){
	         				lbChildrenArray.push({"value":success[0].children[i].children[j].id,"label":success[0].children[i].children[j].text});
	         			}
	         		lbArray.push({"value":success[0].children[i].id,"label":success[0].children[i].text,children:lbChildrenArray});
	         		}else{
	         			lbArray.push({"value":success[0].children[i].id,"label":success[0].children[i].text});
	         		}
	         	}
			    this.ruleForm.lboptions=lbArray;
                for(let i=0;i<this.ruleForm.lboptions.length;i++){
	            	for(let j=0;j<this.ruleForm.lboptions[i].children.length;j++){
	            		if(this.ruleForm.lboptions[i].children[j].value==this.$route.query.row.lbid){
	            			this.ruleForm.lbidArr=[this.ruleForm.lboptions[i].value,this.$route.query.row.lbid];
	            			this.ruleForm.lbmc=this.$route.query.row.lbmc;
	            		    this.ruleForm.lbid=this.$route.query.row.lbid;
	            		}
	            	}
	            }
			    
			  },
			failure=>{
			   console.log("获取类别列表失败")
			})
		 	this.$api.page('dw/getErpDwList',  {},localStorage.token, success => {
	         	let dwArray=new Array();
	         	dwArray.length=0;
	         	for(let i=0;i<success.length;i++){
	         		dwArray.push({"value":success[i].dwmc,"label":success[i].dwmc});
	         	}
			   this.ruleForm.dws=dwArray;
			  },
			 failure=>{
			   console.log("获取单位列表失败");
			 })		 	
		 	this.$api.page('sizeinfo/getSizeinfos',  {},localStorage.token, success => {
	         	let cmArray=new Array();
	         	cmArray.length=0;
	         	for(let i=0;i<success.length;i++){
	         		cmArray.push({"value":success[i].cmmc,"label":success[i].cmmc});
	         	}
			   this.ruleForm.cms=cmArray;
			  },
			 failure=>{
			   console.log("获取尺码列表失败");
			 })
		 	this.ruleForm.spid=this.$route.query.row.id;
            this.ruleForm.spmc=this.$route.query.row.spmc;
            this.ruleForm.itemno=this.$route.query.row.itemno;
            this.ruleForm.dw=this.$route.query.row.dw;
            this.ruleForm.spcode=this.$route.query.row.spcode;
            this.ruleForm.cz=this.$route.query.row.cz;
            this.ruleForm.brand=this.$route.query.row.brand;
            this.ruleForm.costprice=this.$route.query.row.costprice;
            this.ruleForm.productdesc=this.$route.query.row.productdesc;
            this.scaleClass="scaleClass"
            this.$api.get('product/getProductInfoByProductId/'+this.ruleForm.spid,  {},localStorage.token, success => {
            	this.ruleForm.tableData=success.obj.mxlist;
            	console.log(this.ruleForm.tableData);
            	for(let i=0;i<this.ruleForm.tableData.length;i++){
            		this.ruleForm.tableData[i]['index']=i;
            	}
            	
            },failure=>{
			   console.log(failure)
			})
        },
		methods: {
			submitForm() {
				this.$refs.ruleForm.validate((valid)=>{
					if(valid){
						 this.loading = true;
						 if(this.ruleForm.tableData.length==0){
						 	this.loading = false;
						 	this.$message({
					          message: '表单填写不完整，请检查是否填写详情信息',
					          duration:'2500',
					          type:'warning'
				         	});
						 }else{
						 	this.$api.post('product/updProductInfo',{
						 	'id':this.ruleForm.spid,
						 	'spmc':this.ruleForm.spmc,
						 	'spcode':this.ruleForm.spcode,
						 	'dw':this.ruleForm.dw,
						 	'lbid':this.ruleForm.lbid,
						 	'lbmc':this.ruleForm.lbmc,
						 	'cz':this.ruleForm.cz,
						 	'brand':this.ruleForm.brand,
						 	'costprice':this.ruleForm.costprice,
						 	'productdesc':this.ruleForm.productdesc,
						 	'itemno':this.ruleForm.itemno,
						 	'erpSpmxes':this.ruleForm.tableData
						 },localStorage.token, success => {
						 	this.$message({
					          message: success.msg, //商品信息添加成功
					          duration:'1000',
					          type:'success'
			         		});
				 			this.$router.push({ path: '/nav/sptb/sptb' });
						  },
						 failure=>{
						  this.loading=false;
						  this.$message({
					          message: failure.msg, //商品信息添加成功
					          duration:'1000',
					          type:'error'
			         		});
						 })
						 }
					}else{
						this.$message({
					          message: '表单填写不完整，请检查基本信息是否填写完整',
					          duration:'2500',
					          type:'warning'
			         	});
					}
				})
			},
			resetForm() {
				this.$message({
					message: '重置成功',
					type: 'warning'
				});
			},
			handleChangelb(){
			this.$api.page('product/getProductCodeByLbid/'+this.ruleForm.lbidArr[1],  {},localStorage.token, success => {
		    this.ruleForm.spcode=success.obj;
			  	for(let i=0;i<this.ruleForm.lboptions.length;i++){
			  		for(let j=0;j<this.ruleForm.lboptions[i].children.length;j++){
			  			if(this.ruleForm.lboptions[i].children[j].value==this.ruleForm.lbidArr[1]){
			  				this.ruleForm.lbmc=this.ruleForm.lboptions[i].children[j].label;
			  				this.ruleForm.lbid=this.ruleForm.lbidArr[1];
			  			}
			  		}
			  	}
			  },
			 failure=>{
			   console.log("获取商品编码失败")
			 })
			},
			handleCmYsSl(){
				if(this.form.cm){
					this.form.ys="";
			        this.form.jdsl="";
					this.dialogFormVisible=true;
				}else{
					 this.$message({
			          message: '请选择尺码',
			          type: 'warning'
			        });
				}
				/*
				if(this.ruleForm.cm){
					this.$prompt('请为'+this.ruleForm.cm+"指定数量", '系统提示', {
				          confirmButtonText: 'OK',
				          cancelButtonText: 'Cancel',
				          inputPattern:  /^\+?[1-9]\d*$/,
				          inputErrorMessage: '数量不能小于0',
				          inputType:'number'
			        }).then(({ value }) => {
			            this.ruleForm.jdsl=value
          				this.$prompt('请为'+this.ruleForm.cm+"指定颜色", '系统提示', {
					        confirmButtonText: 'OK',
					        cancelButtonText: 'Cancel',
					        inputPattern:      /\s*\S+?/,
				            inputErrorMessage: '颜色必填',
				            inputType:'text',
				        }).then(({ value }) => {
				        this.ruleForm.ys=value
				        this.ruleForm.tableData.push({"cm":this.ruleForm.cm,"jdsl":this.ruleForm.jdsl,"ys":this.ruleForm.ys});
				        }).catch(() => {
				               
				        });
			          
			        }).catch(() => {
			               
			        });
				}else{
					 this.$message({
			          message: '请选择尺码',
			          type: 'warning'
			        });
				}*/
			},
			addYsSl(){
				this.$refs.form.validate((valid)=>{
					if(valid){
						let flag=false;
						let idx=0;
						if(this.ruleForm.tableData.length>0){
							for(let i=0;i<this.ruleForm.tableData.length;i++){
								if(this.form.cm==this.ruleForm.tableData[i].cm&&this.form.ys==this.ruleForm.tableData[i].ys){
									flag=true;
									idx=i;
								}
							}
							if(flag){
								 this.$confirm('所要添加的尺码+颜色在列表中已经存在', 'Warning', {
						          confirmButtonText: '覆盖',
						          cancelButtonText: '取消',
						          type: 'warning',
						          center: true
						       }).then(() => {
						          this.ruleForm.tableData[idx].jdsl=parseInt(this.ruleForm.tableData[idx].jdsl)+parseInt(this.form.jdsl);
						          this.dialogFormVisible=false;
						        }).catch(() => {
						          this.dialogFormVisible=false;
						        });
							}else{
								 this.ruleForm.tableData.push({"id":this.ruleForm.tableData.length,"cm":this.form.cm,"jdsl":this.form.jdsl,"ys":this.form.ys});
						         this.dialogFormVisible=false;
							}
						}else{
							 this.ruleForm.tableData.push({"id":this.ruleForm.tableData.length,"cm":this.form.cm,"jdsl":this.form.jdsl,"ys":this.form.ys});
					         this.dialogFormVisible=false;
						}
					}
				})
			},
			updCmYsSl(id){
				this.dialogFormCmYsSlVisible=true;
				for(let i=0;i<this.ruleForm.tableData.length;i++){
					if(this.ruleForm.tableData[i].id==id){
						this.forms.id=this.ruleForm.tableData[i].id;
						this.forms.cms=this.ruleForm.tableData[i].cm;
						this.forms.yss=this.ruleForm.tableData[i].ys
						this.forms.jdsls=this.ruleForm.tableData[i].jdsl
					}
				}
			},
			updateCmYsSl(id){
				this.$refs.forms.validate((valid)=>{
					if(valid){
					    this.dialogFormCmYsSlVisible=false;
						for(let i=0;i<this.ruleForm.tableData.length;i++){
								if(this.ruleForm.tableData[i].id==id){
									this.ruleForm.tableData[i].cm=this.forms.cms
									this.ruleForm.tableData[i].ys=this.forms.yss
									this.ruleForm.tableData[i].jdsl=this.forms.jdsls
								}
						}
					}else{
						
					}
				})
			},
			delteleCmYsSl(index){
				for(let i=0;i<this.ruleForm.tableData.length;i++){
					if(this.ruleForm.tableData[i].index==index){
						
						this.ruleForm.tableData.splice(index,1)
					}
				}
				for(let i=0;i<this.ruleForm.tableData.length;i++){
					this.ruleForm.tableData[i]["id"]=i;
				}
			},
			gotoParentPage() {
				this.$router.push({ path: '/nav/sptb/sptb' }); 
			}
		}
	}


